// AdminLayout.js
import { Navigate, Outlet } from "react-router-dom";


const permission = [
  "Distributor",
  "Super Distributor",
  "asm",
  "zsm",
]

const NonRetailerProtect = () => {
  const userType  =  localStorage.getItem("userType"); 
  
  if (!permission.includes(userType)) {
    localStorage.clear();
 

    return <Navigate to="/login-area" replace />;
  }

  return (
  
      <Outlet /> 
 
  );
};

export default NonRetailerProtect;
